import { BlueBase, BlueBaseApp } from '@bluebase/core';
import Plugin from '../index';
import { Text } from 'react-native';
// import WithApolloProvider from '../withApolloProvider';
import React from 'react';
import { mount } from 'enzyme';
import { waitForElement } from 'enzyme-async-helpers';

const CustomText = () => <Text>Everything is ok!</Text>;

test('Plugin correctly registered', async () => {
	const BB = new BlueBase();
	await BB.Plugins.register(Plugin);

	expect(BB.Plugins.has('plugin-apollo')).toBeTruthy();
});


test('Plugin should throw error if httpLinkOptions config is not given', async () => {
	
	const BB = new BlueBase();
	await BB.Plugins.register(Plugin);

	let message;

	try {
		await BB.boot();
	} catch (error) {
		message = error.message;
	}

	expect(message).toBe('HTTP Link URI not provided to Apollo');
});


test('Plugin correctly registered', async () => {
	
	const wrapper = mount(
		<BlueBaseApp plugins={[Plugin]} configs={{ 'plugins.apollo.httpLinkOptions': { uri: 'www.google.com' } }}>
			<CustomText />
		</BlueBaseApp>
	);

	
	await waitForElement(wrapper, CustomText);
	expect(wrapper).toMatchSnapshot();
});


